.blog-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;

  .new-blog-link {
    position: fixed;
    right: 4em;
    top: 4em;
    text-align: right;

    a {
      font-size: 3em;
      cursor: pointer;
      color: $teal;
      margin-right: 15px;
      transition: 0.3s ease-in-out;

      &:hover {
        color: $dark-teal;
      }
    }
  }
  .content-container {
    display: flex;
    flex-direction: column;
    width: 800px;

    a {
      text-decoration: none;
      color: $teal;
      transition: 0.5s ease-in-out;

      &:hover {
        color: $dark-teal;
      }
    }

    h1 {
      font-size: 1.5em;
    }
    // hr {
    //   width: 70%; /* controla el ancho */
    //   margin: 2rem auto; /* centra la línea horizontal */
    //   border: none; /* elimina el estilo por defecto */
    //   border-top: 1px solid #004d4d; /* define grosor y color */
    // }
    .line-wrapper {
      display: flex;
      justify-content: center; /* centra horizontalmente */
      align-items: center; /* centra verticalmente */
    }

    hr {
      border: none;
      height: 25px; /* largo de la línea */
      margin: 5px 10px; /* espacio a los lados */
      width: 2px; /* grosor de la línea */

      /* degradado vertical con $dark-teal */
      background: $dark-teal;
    }

    .featured-image-wrapper {
      img {
        margin: 15px 0px;
        width: 100%;
        height: 350px;
        border-radius: 50px;
        object-fit: cover;
      }
    }
    .blog-content-wrapper {
      //box-shadow: 0 4px 6px -2px rgba(0, 0, 0, 0.2);
    }
    .blog-item-title {
      display: flex;
      align-items: center;
      justify-content: left;
      gap: 15px;
    }

    .blog-item-title h1 {
      margin-right: 15px;
      font-size: 1.5rem;
    }

    .blog-item-title svg {
      color: $dark-teal;
    }
  }
  .admin-blog-wrapper {
    margin-bottom: 20px;
    padding: 10px 0;
    //border-bottom: 1px solid lighten($charcoal, 60%);

    /* ESTILO DEL ENLACE DE BORRAR */
    a.delete-blog {
      display: inline;
      float: right;
      font-size: 0.8em;
      font-weight: 700;
      margin-right: 3.5em;
      /* SOBRESCRITURA DEL COLOR $teal */
      color: $charcoal;
      cursor: pointer;
      transition: color 0.3s ease-in-out;

      &:hover {
        color: $warning; /* Color de advertencia al hacer hover */
      }

      svg {
        /* Asegura que el icono también cambie con el texto */
        color: inherit;
        margin-left: 5px;
      }
    }
  }
}
.blog-back-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  // height: 100vh;
  // width: 100vw;
}

.blog-back-wrapper a {
  font-size: 3em;
  color: $teal;
  cursor: pointer;
  text-decoration: none;

  &:visited {
    color: $teal;
  }

  &:hover {
    color: $dark-teal;
  }
}
.h1-to-edit {
  cursor: pointer;
}
