.left-wrapper {
  width: 100%;
  max-width: 1200px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 20px auto;
  position: relative;
}
.left-wrapper .linea-estilizada {
  position: absolute;
  width: 100%;
  height: 2px;
  border: none;
  background-color: #333;
  z-index: 0;
  margin: 0;
  top: 50%;
  transform: translateY(-50%);
}
.left-wrapper .left-silk {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.left-wrapper .left-silk h1 {
  color: #004d4d;
  font-family: "Optima", sans-serif;
  font-weight: 600;
  font-size: 1.3em;
  letter-spacing: 2px;
  margin: 0;
  padding: 0 25px;
  background-color: #f6f6f6;
  z-index: 2;
}